@import "../../style.scss";

.comments{
    @include themify($themes){
        img{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }

        .write{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin: 20px 0;
            input{
                flex: 5;
                padding: 10px;
                background-color: transparent;
                border: 1px solid themed("border");
                color: themed("textColor");
            }
            input:focus{
                outline: none;
            }
            button{
                border: none;
                background-color: #5371ff;
                color: white;
                padding: 10px;
                cursor: pointer;
                border-radius: 3px;
            }
        }
        
        .comment{
            margin: 30px 0;
            display: flex;
            justify-content: space-between;
            gap: 20px;
            .info{
                flex: 5;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 3px;
                span{
                    font-weight: 500;
                }
                p{
                    color: themed("textColorSoft");
                }
            }
            .date{
                flex: 1;
                align-self: center;
                color: gray;
                font-size: small;
            }
        }
    }
}